<template>
  <v-input
    :value="value"
    class="v-ext-input-fallback"
    @input="$emit('input', $event)" />
</template>

<script>
export default {
  name: "v-ext-input-fallback",
  props: {
    value: {
      type: null,
      default: ""
    },
    inputName: {
      type: String,
      default: ""
    },
    name: {
      type: String,
      default: ""
    }
  },
  created() {
    if (this.inputName == null) {
      this.$notify.alert(
        this.$t("no_interface_error", {
          field: this.$helpers.formatTitle(this.name)
        })
      );
    } else {
      this.$notify.alert(this.$t("extension_error", { ext: this.inputName }));
    }
  }
};
</script>

<style lang="scss" scoped>
.v-ext-input-fallback {
  max-width: var(--width-medium);
}
</style>
